//
// RESET
//
// https://meyerweb.com/eric/tools/css/reset/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
*::after, 
*::before { 
	box-sizing: border-box;
}

*:focus {
    outline: 0 !important;
}

body {
	background-color: #FFFFFF;
	font-family: -apple-system, 'Nunito', 'Helvetica Neue', sans-serif;
	font-weight: 300px;
	margin: 0;
	padding: 0;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.loading-spinner circle {
	-webkit-animation-name: loadingSpinner;
	        animation-name: loadingSpinner;
	-webkit-animation-duration: 2s;
	        animation-duration: 2s;
	-webkit-animation-timing-function: cubic-bezier(0.05, 0.2, 0.35, 1);
	        animation-timing-function: cubic-bezier(0.05, 0.2, 0.35, 1);
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
}

.loading-spinner circle:nth-child(2) {
	-webkit-animation-delay: 0.16s;
	        animation-delay: 0.16s;
}

.loading-spinner circle:nth-child(3) {
	-webkit-animation-delay: 0.32s;
	        animation-delay: 0.32s;
}

@-webkit-keyframes loadingSpinner {
	0% {
		opacity: 0;
		-webkit-transform: translateY(0);
		        transform: translateY(0);
   }
	25% {
		opacity: 1;
		-webkit-transform: translateY(-10px);
		        transform: translateY(-10px);
   }
}

@keyframes loadingSpinner {
	0% {
		opacity: 0;
		-webkit-transform: translateY(0);
		        transform: translateY(0);
   }
	25% {
		opacity: 1;
		-webkit-transform: translateY(-10px);
		        transform: translateY(-10px);
   }
}
